.container {
  min-width: 400px;
  display: flex;
  flex-direction: column;
  border-radius: 13px;
  color: white;
  height: fit-content;

  text-align: center;
  padding: 10px 0px;
  h1 {
    color: #e5d5d5;
  }
}

.header {
  padding: 10px;
  display: flex;
  flex-direction: column;
  row-gap: 14px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  border-bottom: 1px solid white;
}

.title {
  font-size: 30px;
  font-weight: bold;
}

.info {
  padding: 15px;
  color: white;
  border-bottom: 1px solid white;
  .userCount {
    h1 {
      color: white;
    }
    font-size: 18px;
  }
}

.userCount {
  font-size: 16px;
  font-weight: bold;
}

.price {
  font-size: 16px;
  font-weight: bold;
}
.feature_item {
  display: flex;
  align-items: center;
  column-gap: 5px;
}
.storage {
  font-size: 16px;
}

.features {
  display: flex;
  flex-direction: column;
  margin: 10px;
  justify-content: center;
}

.feature {
  display: flex;
  flex-direction: column;
  row-gap: 5px;
  text-align: left;
}
.feature__img {
  width: 16px;
  height: 16px;
}
.tarrif_btn {
  padding: 20px;
  button {
    padding: 8px 16px;
    border-radius: 13px;
    background: rgba(255, 255, 255, 0.43);
    cursor: pointer;
    border: none;
    color: white;
    &:hover {
      background: rgba(255, 255, 255, 0.5);
    }
  }
}
@media (max-width: 1600px) {
  .container {
    min-width: 300px;
  }
}
@media (max-width: 1400px) {
  .container {
    min-width: 280px;
  }
}
@media (max-width: 780px) {
  .container {
    min-width: 300px;
  }
}
@media (max-width: 500px) {
  .container {
    min-width: 300px;
  }
}
